@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Marck+Script&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Oswald:wght@200..700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Tenor+Sans&display=swap');

input[type=text], input[type=submit], textarea{
	-webkit-appearance: none;
} 

.App {
  
  text-align: center;
  background-color: #edf2f4;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: "Montserrat", sans-serif;
}

h1 {
  margin-top: 150px;
  color: #2b2d42;
  font-size: 50px;
}

.main {
  
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 65px;
}

.counter {
  font-size: 65px;
  color: #2b2d42;
}

#plus {
  background-color: #ef233c;
}

.btn {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 0px;
  outline: 0px;
  background-color: #8d99ae;
  color: azure;
  font-size: 75px;
  &:hover {
    opacity: 0.7;
  }
  transition-duration: 0.7s;
  font-family: "Montserrat", sans-serif;
  cursor: pointer;
}

@media(max-width: 1200px) {
  h1 {
    margin-top: calc(60px + (150 - 60) * ((100vw - 320px) / (1200 - 320)));
    font-size: calc(30px + (50 - 30) * ((100vw - 320px) / (1200 - 320)));
  }

  .btn {
    width: calc(70px + (100 - 70) * ((100vw - 320px) / (1200 - 320)));
    height: calc(70px + (100 - 70) * ((100vw - 320px) / (1200 - 320)));
    font-size: calc(50px + (75 - 50) * ((100vw - 320px) / (1200 - 320)));
  }

  .main {
    gap: calc(40px + (65 - 40) * ((100vw - 320px) / (1200 - 320)));
  }
}








